import React from 'react'
import { graphql } from 'gatsby'
import Layout from '../layouts/Layout'

export default ({ data }) => (
  <Layout>
    <h1>{data.site.siteMetadata.title} O nas</h1>
    <p>Dowiedz się więcej o Fundacji!</p>
  </Layout>
)

export const query = graphql`
  query AboutQuery {
    site {
      siteMetadata {
        title
      }
    }
  }
`